@import "src/styles/core/variables";
@import "src/styles/core/mixins";

.header {
  font-family: $font-title;
  text-align: center;
  background-color: $background-ColorBody-g;

  & .title {
    padding-top: 1rem;
    margin-top: 0;
    font-size: 1rem;
  }

  & .photo {
    width: 17.5rem;
    padding-bottom: 1rem;
  }
}

.usersContainer {
  background-color: $background-Users;
}
.wrapperMain {
  font-family: $font-title;

  .users {
    font-size: 1.2rem;
    @include usersSubtitle;
  }

  .usersLink {
    @include link;
    font-size: 1.1rem;
  }

  .usersList {
    @include flex-row;
    flex-wrap: wrap;
  }

  .listName {
    @include flex-column;
    background-color: $background-ColorUsers;
    height: 3rem;
    min-width: 6rem;
    border-radius: 10%;
    margin: 0.3rem;
    text-decoration: none;

    &:hover {
      background-color: $Hover-BgColorUsers;
    }
  }

  .usersExpense {
    @include usersSubtitle;
    padding-top: 2rem;
  }

  .expenseLink {
    @include link;
    padding-left: 3rem;
    font-size: 1.2rem;
  }
}

.balance {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.boxBalance {
  @include flex-row;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  border-bottom: 1px solid rgba(111, 173, 175, 0.37);
}
